@import "../../styles/utils.scss";
@include mobile {
  .search-bar {
    width: 40%;
    @include flex(row, center, center);
    .form-container {
      @include flex(row, center, none);
      width: 85%;
      height: 20px;
      border: none;
      input {
        width: 100%;
        height: 100%;
        padding: 5px;
        font-size: 12px;
        border: none;
        border-radius: 10px;
      }
      label {
        @include flex(row, none, center);
        input[type="radio"] {
          padding-left: 10px;
          margin-right: 10px;
        }
      }
    }
  }
}
@include tablet {
  .search-bar {
    width: 60%;
    @include flex(row, center, center);
    .form-container {
      @include flex(row, center, none);
      width: 90%;
      height: 20px;
      border: none;
      input {
        width: 100%;
        height: 100%;
        padding: 7px;
        font-size: 14px;
        border: none;
        border-radius: 10px;
      }
      label {
        @include flex(row, none, center);
        input[type="radio"] {
          padding-left: 10px;
          margin-right: 10px;
        }
      }
    }
  }
}
@include desktop {
  .search-bar {
    width: 70%;
    @include flex(row, center, center);
    .form-container {
      @include flex(row, center, none);
      width: 90%;
      height: 20px;
      border: none;
      input {
        width: 100%;
        height: 100%;
        padding: 7px;
        font-size: 14px;
        border: none;
        border-radius: 10px;
      }
      label {
        @include flex(row, none, center);
        input[type="radio"] {
          padding-left: 10px;
          margin-right: 10px;
        }
      }
    }
  }
}
