@import "../../styles/utils.scss";
@include mobile {
  .user-review {
    .review-container {
      @include flex(row, none, none);
      margin: 5px;
      border-bottom: 1px solid #e3e3e3;
      animation: fadeIn 1.5s ease-out;
      div:nth-child(1) {
        img {
          height: 100px;
        }
      }
      div:nth-child(2) {
        margin: 5px;
        width: 100%;
        div {
          height: 100%;
          width: 100%;
          @include flex(column, none, center);
          .like-container {
            display: flex;
            align-items: center;
            margin-top: 5px;
            span {
              margin-right: 5px;
              &.likebtn {
                cursor: pointer;
              }
            }
          }
          .review-like,
          .review-date,
          .review-book,
          .review-text {
            font-size: 0.7rem;
          }
          .review-book,
          .review-text {
            margin-bottom: 10px;
          }

          .review-date {
            color: $dark-grey;
          }
        }
      }
      .review-edit,
      .review-remove {
        cursor: pointer;
      }
    }
  }
}
