@import "../../styles/utils.scss";
@include mobile {
  .user-profile {
    @include base-height;
    margin-top: 90px;
    .profile-wrapper {
      @include flex(row, center, center);
      border-bottom: 5px solid #e3e3e3;
      margin-bottom: 10px;
      margin-top: 10;
      .info {
        @include flex(column, center, center);
        img {
          width: 110px;
          height: 110px;
          border-radius: 50%;
          animation: fadeIn 1.5s ease-out;
          cursor: pointer;
          &:hover {
            opacity: 0.7;
          }
        }
        h1 {
          margin: 10px;
        }
        .intro {
          margin: 10px;
          font-size: 0.8rem;
        }
        .rate-review {
          margin-top: 0px;
          text-align: center;
          p {
            margin: 5px;
            font-size: 0.8rem;
          }
        }
        .profile-edit {
          margin-bottom: 10px;
          p {
            background-color: $blue;
            padding: 5px;
            border-radius: 10px;
            font-size: 0.6rem;
            color: white;
            cursor: pointer;
          }
        }
      }
    }
    .catalog-list {
      @include flex(row, center, center);
      text-align: center;
      li {
        border-bottom: 2px solid $dark-grey;
        margin: 5px;
        cursor: pointer;
        &.active {
          border-bottom: 2px solid $sun-flower;
        }
      }
    }
    .uploaded,
    .book-shelve,
    .currently-reading,
    .want-read,
    .read,
    .recent-update {
      margin-top: 10px;
      margin-left: 10px;
      margin-right: 10px;
      padding-top: 10px;
      width: 80%;
      margin: 0 auto;
      @include flex(column, none, none);
    }
    .book-shelve-head {
      @include flex(row, center, space-between);
      span {
        cursor: pointer;
      }
      .add-container {
        @include flex(row, center, center);
        .add-btn {
          @include flex(row, none, none);
          svg {
            margin-right: 5px;
            margin-top: 1px;
          }
        }
      }
    }
    .book-shelve-body {
      padding-bottom: 10px;
      .big-container {
        margin-top: 10px;
        display: flex;
        width: 100%;
        flex-wrap: wrap;
        div {
          color: $dark-grey;
          cursor: pointer;
          margin: 5px;
        }
      }
    }
    .uploaded-head,
    .book-shelve-head,
    .recent-head,
    .currently-head,
    .want-read-head,
    .read-head {
      border-bottom: 1px solid #e3e3e3;
      cursor: pointer;
    }
    .uploaded-body,
    .recent-body,
    .currently-body,
    .want-read-body,
    .read-body {
      padding-bottom: 10px;
      .recent-empty {
        color: $dark-grey;
        margin: 10px 5px;
      }
    }
    .uploaded-list {
      @include flex(row, none, none);
      overflow-x: scroll;
      .uploaded-img-wrapper {
        position: relative;
        margin: 5px;
        .uploaded-img {
          height: 85px;
        }
        .to-detail {
          position: absolute;
          top: 0;
          width: 100%;
          left: 0;
          height: 95%;
          text-align: center;
          font-size: 0.7rem;
          //@include flex(row, center, center);
          opacity: 0;
          cursor: pointer;
          &:hover {
            opacity: 1;
            background-color: #1f1f1f5e;
            font-weight: 100;
          }
          a {
            @include flex(row, center, center);
            color: white;
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
  .modal-overay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.22);
    animation: fadeIn 1.5s ease-out both;
  }
  .open-modal {
    display: block;
  }
  .close-modal {
    visibility: hidden;
    display: none;
  }
}
