@import "../../styles/utils.scss";
@include mobile {
  .book-shelve {
    padding: {
      top: 80px;
    }
    min-height: 100vh;
    .shelve-header {
      padding: {
        left: 10px;
        bottom: 10px;
      }
      margin: {
        top: 10px;
      }
      border-bottom: 1px solid #e3e3e3;
      width: 100%;
      background: white;
      &.fix {
        position: fixed;
        width: 100%;
        margin-top: 0;
        padding-top: 10px;
        background: white;
        top: 63px;
      }
    }
    .menu-ul {
      @include flex(row, center, center);
      &.fix {
        padding-top: 40px;
      }
      li {
        padding: 10px;
        flex: 1;
        text-align: center;
        cursor: pointer;
        font-size: 0.7rem;
      }
      & .show {
        font-weight: 800;
      }
    }
    table {
      @include flex(column, center, center);
      margin: 30px auto;
      width: 100%;
      thead {
        width: 85%;
        border: 1px solid $sun-flower;
        border-top-color: transparent;
        border-left-color: transparent;
        border-right-color: transparent;
        tr {
          @include flex(row, center, flex-start);
          th {
            @include table-text-align(10px, center, 0.6rem);
            &:nth-child(1) {
              width: 10%;
            }
            &:nth-child(2) {
              width: 20%;
              display: none;
            }
            &:nth-child(3) {
              width: 10%;
              text-overflow: ellipsis;
              overflow: hidden;
              white-space: pre;
            }
            &:nth-child(4) {
              width: 10%;
            }
            &:nth-child(5) {
              width: 20%;
            }
          }
        }
      }
      tbody {
        width: 85%;
        tr {
          @include flex(row, flex-start, flex-start);
          border-bottom: 1px solid $white-grey;
          td {
            @include table-text-align(10px, center, 0.5rem);
            word-break: keep-all;
            &:nth-child(1) {
              width: 10%;
              img {
                width: 100%;
              }
            }
            &:nth-child(2) {
              display: none;
              width: 20%;
              white-space: pre;
              overflow: hidden;
              text-overflow: ellipsis;
              &:hover a {
                color: $sun-flower;
              }
            }
            &:nth-child(3) {
              width: 10%;
              text-overflow: ellipsis;
              overflow: hidden;
              white-space: pre;
            }
            &:nth-child(4) {
              width: 10%;
            }
            &:nth-child(5) {
              width: 20%;
            }
            &:nth-child(7) {
              @include flex(row, center, center);
              cursor: pointer;
              padding: 5px;
              span {
                margin: 5px;
              }
              & span:nth-child(1) {
                color: $sun-flower;
              }
              & span:nth-child(2) {
                font-weight: 800;
              }
            }
          }
        }
      }
    }
  }
}
